import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController,
} from "ionic-angular";
import { WarningCheckViewModel } from "../../Model/ViewModel/WarningCheckViewModel";

/**
 * Generated class for the WarningCheckModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page warning-check-add-edit-modal",
  templateUrl: "warning-check-add-edit-modal.html"
})
export class WarningCheckAddEditModalPage {
  item: WarningCheckViewModel;
  mode: string;
  title: string;
  CanEditJob: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,

  ) {
    this.item = new WarningCheckViewModel();
    this.item.schemaname=navParams.data.item.schemaname;
    this.item.tablename=navParams.data.item.tablename;
    this.item.columnname=navParams.data.item.columnname;
    this.item.check_key=navParams.data.item.check_key;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;


    this.mode=navParams.data.mode;
    this.CanEditJob = navParams.data.CanEditJob;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectCheckKey() {
    let modal = this.modalCtrl.create("WarningCheckSelectModalPage", {
      item: this.item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.check_key = select_data.code_key;
       this.item.check_desc=select_data.code_desc;
    });

    modal.present();
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad WarningCheckAddEditModalPage");
  }
}



